.userList-container-m {
  background: #fafbff;
  margin-top: 20px;
  border-radius: 6px;
  .show-all-markets-m {
    position: static;
    display: inline-flex;
    padding-right: 5px;
    > span {
      border-bottom: 1px dashed #545669;
      padding: 0;
      margin-left: 10px;
      > span {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;
      }
    }
    .ant-checkbox {
      position: relative;
      top: -1px;
      border: none;
    }
    .ant-checkbox-inner {
      width: 12px;
      height: 12px;
      background: #d1d7e9;
      border-radius: 2px;
      border-bottom: none;
    }
    .ant-checkbox-checked .ant-checkbox-inner {
      background: #3d56d6;
    }
    &.ant-checkbox-wrapper-checked {
      > span {
        border-bottom: 1px dashed #3d56d6;
        > span {
          color: #3d56d6;
        }
      }
    }
    .ant-checkbox-inner::after {
      border-width: 1px;
    }
  }
  .ant-tabs-nav-wrap,
  .ant-tabs-nav-list {
    width: 100%;
  }
  .ant-tabs-nav-wrap {
    // border-bottom: 1px solid rgba(15, 19, 79, 0.05);
  }
  .ant-tabs-tab {
    width: 50%;
    justify-content: center;
    padding: 10px 0;
    &.ant-tabs-tab-active {
      .ant-tabs-tab-btn,
      .tabname-m {
        font-weight: 600;
        color: #3d56d6;
        text-shadow: none;
      }
    }
  }
  .ant-tabs-tab-btn {
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #0f134f;
  }
  .tabname-m {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #0f134f;
  }
  .userTable .warning-tip {
    width: calc(100% + 20px);
    transform: translateX(-10px);
    border: none;
  }
  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn::after {
    display: none;
  }
}
